import React from 'react';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';


//matirial imports
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';

import './login.css'

class UserNameForm extends React.Component {

    state = {
        userName:"",
        userNameValidation:""
    }

    userNameValidation=(event)=>{
        if(event.target.value==="") this.setState({userNameValidation:"User name is requierd"})
        else this.setState({userNameValidation:""})
    }

    checkUserName=()=>{
        this.props.checkUserName(this.state.userName)
        
    }

    render(){
        return(

                <>
                    <div className="row wellcome-title">
                        To continue your account
                        <br />
                        just enter user name.
                    </div>
                    <form noValidate autoComplete="off">
                        <TextField
                            id="user name"
                            label="user name"
                            fullWidth
                            InputLabelProps={{
                                shrink: true,
                            }}
                            margin="dense"
                            onChange={e=>this.setState({userName:e.target.value})}
                            value={this.state.userName}
                            helperText={this.state.userNameValidation}
                            onBlur={this.userNameValidation}
                        />
                        <div className="row sign-in-up-button-container">
                                            
                            <button
                                disabled={
                                    this.state.userName!==""?
                                    false:true
                                }
                                type="button"
                                className="col-xl-4 col-lg-4 col-md-4 col-sm-12 btn btn-pink"
                                onClick={this.checkUserName}
                            >
                                next
                            </button>
                        </div>
                    </form>
               
                </>

        );
    }
}

const mapDispatchToProps=(dispatch)=>{
    return{
        checkUserName:(userName) => dispatch(actions.checkUserName(userName))
    }
}

export default connect(null, mapDispatchToProps)(UserNameForm)