import { actions } from '../actions/Action';
import axios from 'axios'
import { auth } from "../../config/firebase";
import keys from '../../config/keys';
import { getJwtFromCookie } from '../../cookie/cookie'
import { useHistory } from "react-router-dom";

import history from '../../config/history'





export const onAuthStateChanged = ({ dispatch, getState }) => next => action => {

    if (action.type === 'ON_AUTH_STATE_CHANGED') {

        auth.onAuthStateChanged(user => {
            
            if (user) {

                console.log(user);
                auth.currentUser.getIdToken(true)
                .then(firebaseToken=> dispatch(actions.getAccessToken(firebaseToken)))
                .catch(error=>console.log(error));
            }
        });
      
    }
    return next(action);
};

export const getAccessToken = ({ dispatch, getState }) => next => action => {

    if (action.type === 'GET_ACCESS_TOKEN') {

        const config={
            headers: { 
                dataType: "json",
                contentType: "application/json",
            }
        };

        const data={
            action: "firebaseloginwithcredentials",
            jwt: action.payload,
        }

        dispatch(actions.setLoading());

        axios.post(`${keys.API_URL_LOGIN}/getAccessToken`,data,config)
        .then(res=>dispatch(actions.checkPermission(res.data)))
        .catch(err=>console.log(err));

    }
    return next(action);
};

export const checkPermission = ({ dispatch, getState }) => next => action => {

    if (action.type === 'CHECK_PERMISSION') {

        let TokenToString = action.payload.accessToken.toString();

        const data = {
            action: "loginCheckPermission",
            token: TokenToString
        };

        const config={
            headers: {
                Authorization: TokenToString
            },
            dataType: "json",
            contentType: "application/json",
            withCradentials: true,
        }
    
        axios.post(`${keys.API_URL_LOGIN}/checkPermission`,data,config)
        .then(res=>dispatch(actions.setJwt(res.data)))
        .catch(err=>console.log(err));

    }
    return next(action);
};

export const setJwt = ({ dispatch, getState }) => next => action => {

    if (action.type === 'SET_JWT') {

        console.log('in success checkPermission');

        let jsonWebToken = action.payload.jwt;
        let usename = action.payload.userName;

        console.log(usename,action.payload.is_username)

        let noQuotesJwtData = jsonWebToken.split('"').join("");
        let now = new Date();
        now.setMonth(now.getMonth() + 1);
        document.cookie = "jwt=" + noQuotesJwtData + ",domain=.knowme.page" + ", path=/, Expires="+now.toUTCString()+","
        
        // dispatch(actions.setJwt(noQuotesJwtData))
        dispatch(actions.setLoading());

        if(usename===""){
            dispatch(actions.setShowUserNameForm(true))
        }
        else{
            window.location.href=`${keys.API_URL_BASE_CLIENT}/admin/${usename}/AllCards`
            // history.push(`/admin/${usename}/AllCards`);
        }

    }
    return next(action);
};

export const checkUserName = ({ dispatch, getState }) => next => action => {

    if (action.type === 'CHECK_USER_NAME') {

        const jwt = getJwtFromCookie('jwt')
        const data = {
            token: jwt,
            usernameToCheck: action.payload
        }
        const config={
            headers: {
                'Content-Type': 'application/json',
                'Authorization': jwt
            },
        }

        axios.post(`${keys.API_URL_LOGIN}/checkUsername`,data,config)
        .then(res=>{
            if(!res.data.availability){
                dispatch(actions.setMessage('The user name is already taken. Please choose another username.'))
            }
            else{
                // dispatch(actions.setMessage('Username available and created!'))
                window.location.href=`${keys.API_URL_BASE_CLIENT}/admin/${action.payload}/AllCards`

                // history.push(`/admin/${action.payload}/AllCards`);
            }
        })
        .catch(err=>console.log(err));

    }
    return next(action);
};
