import produce from 'immer';
import createReducer from "./reducerUtils";
import { setFile } from '../../Files/file'

const initialState = {

    currentVideo: {
        videoShow: true,
        titleVideo: "Video",
        titleVideoShow: true,
        codeHtmlIframe: "",
        videoCard: {
            playing: false,
            playbackRate: 1,
            url: '',

        }
    }
};
const video = {
    setCurrentVideo(state, action) {
        state.currentVideo = action.payload;
    },
    // setplayingVideo(state, action) {
    //     state.currentVideo.videoCard.playing = !state.currentVideo.videoCard.playing;
    // },
    // setSpeedVideo(state, action) {
    //     state.currentVideo.videoCard.playbackRate = parseFloat(action.payload);
    // },
    setUrlVideo(state, action) {
        state.currentVideo.videoCard.url = action.payload;
    },
    setVideoShow(state, action) {
        state.currentVideo.videoShow = !state.currentVideo.videoShow
    },
    setVideoShow(state, action) {
        state.currentVideo.videoShow = !state.currentVideo.videoShow
    },
    setTitleVideoShow(state, action) {
        state.currentVideo.titleVideoShow = !state.currentVideo.titleVideoShow;
    },
    setCodeHtmlIframe(state, action) {
        state.currentVideo.codeHtmlIframe = (action.payload);
    },

}
export default produce((state, action) => createReducer(state, action, video), initialState);
