import { actions } from '../actions/Action';
import Http, { HttpView, HttpFormdata } from '../../axios/axios'


export const createCoverReveiwsBackground = ({ dispatch, getState }) => next => action => {
    if (action.type === 'SET_COVER_REVEIWS_STYLE') {
        const uid = getState().user.userId, userName = getState().user.userName;
        let formData = new FormData();
        formData.append('profile_reveiws', action.payload.file);
        dispatch(actions.setLoading());
        HttpFormdata.post(`/${userName}/${uid}/upload`, formData)
            .then((response) => {
                let url = `url(${response.data.files.profile_reveiws.url})`
                setTimeout(async () => {
                    await dispatch(actions.setCoverReveiwsBackground(url));
                    dispatch(actions.setLoading());
                }
                    , 4000);
            })
            .catch((error) => {
                console.log(error)
            })
        return;
    }
    return next(action);
};


export const deleteReveiws = ({ dispatch, getState }) => next => action => {
    if (action.type === 'DELETE_REVEIW') {
        const userName = getState().user.userName;
        const newReveiw = getState().reveiws.currentReviews.reveiwsList[action.payload];
        Http.post(`/${userName}/${newReveiw._id}/deleteReveiws`, JSON.stringify(newReveiw))
            .then((response) => {
                dispatch(actions.setDeleteReveiw(response.data));
            })
            .catch((error) => {
                console.log(error);
            })
        return;
    }
    return next(action);
};

