import React from 'react';
import { connect } from 'react-redux';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { actions } from '../../Redux/actions/Action';


//matirial imports
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import InputAdornment from '@material-ui/core/InputAdornment';
import VisibilityIcon from '@material-ui/icons/Visibility';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import Snackbar from '@material-ui/core/Snackbar';


import loginService from './loginService'
import UserNameForm from './userNameForm'
import Loading from '../Loading/Loading'
import './login.css'

class Login extends React.Component {

    state = {
        isSignIn: true,
        RememberMe:false,
        email:"",
        password:"",
        emailValidation:"",
        passwordValidation:"",
        userNameValidation:"",
        showPasword:false,
    }

    componentDidMount(){
        this.props.onAuthStateChanged()
    }

    signIn=async()=>{
        await loginService.signIn(this.state.email,this.state.password)
        .then(res=>console.log(res))
        .catch(err=>{this.props.setMessage(err.message)})
    }

    signUp=async()=>{
        await loginService.signUp(this.state.email,this.state.password)
        .then(res=>console.log("res",res))
        .catch(err=>{this.props.setMessage(err.message)})
    }

    signInWithGoogle=async()=>{
        await loginService.signInWithGoogle()
        .then(res=>console.log("res",res))
        .catch(err=>{this.props.setMessage(err.message)})
    }

    signInWithFacebook=async()=>{
        await loginService.signInWithFacebook()
        .then(res=>console.log("res",res))
        .catch(err=>{this.props.setMessage(err.message)})
    }

    resetPassword=async ()=>{

        if(this.state.email===""){
            this.props.setMessage("Enter your email to reset password")
            return;
        }
        await loginService.resetPassword(this.state.email)
        .then(res=>{this.props.setMessage("You have been notified of your email inbox")})
        .catch(err=>{this.props.setMessage(err.message)})
        
    }

    emailValidation=(event)=>{
        let emailRegex=/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        if(event.target.value==="") this.setState({emailValidation:"Email is requierd"})
        else if (!emailRegex.test(event.target.value)) this.setState({emailValidation:"Email is not valid"})
        else this.setState({emailValidation:""})
    }

  

    passwordValidation=(event)=>{
        let passwordRegex=/^(?=.*[A-Za-z])(?=.*\d)/i
        if(event.target.value==="") this.setState({passwordValidation:"Password is requierd"})
        else if(event.target.value.length<6) this.setState({passwordValidation:"Password must be at least 6 characters"})
        else if(!passwordRegex.test(event.target.value)) this.setState({passwordValidation:"A password must contain at least one digit and one letter"})
        else this.setState({passwordValidation:""})
    }


    render() {

        return (
            
                <div className='container-fluid login-backgtound'>
                    <Snackbar
                        anchorOrigin={{vertical:'bottom', horizontal:'right'}}
                        open={this.props.login.messageStatus}
                        onClose={e=> this.props.setMessageStatus(false)}
                        message={this.props.login.message}
                    />
                    {
                        this.props.load&&
                        <Loading/>
                    }
                    <img src={require("../../img/me.png")} className="me-logo"/>
                    <img
                        src={require("../../img/shapes.png")}
                        className={this.state.isSignIn?"sign-in-shapes":"sign-up-shapes"}
                    />
                    <div className="row">
                        <div className="login-container" >
                            {
                                this.props.login.isShowUserNameForm?
                                <UserNameForm/>:
                                <>
                                    <div className="row">
                                        <div
                                            className={
                                                this.state.isSignIn ?
                                                    "col-6 sign-in-up-titles-selected sign-in-up-titles" :
                                                    "col-6 sign-in-up-titles-not-selected sign-in-up-titles"
                                            }

                                            onClick={e => this.setState({ isSignIn: true })}
                                        >
                                            Sign In
                                        </div>
                                        <div
                                            className={
                                                this.state.isSignIn ?
                                                    "col-6 sign-in-up-titles-not-selected sign-in-up-titles" :
                                                    "col-6 sign-in-up-titles-selected sign-in-up-titles"
                                            }
                                            onClick={e => this.setState({ isSignIn: false })}
                                        >
                                            Sign Up
                                        </div>
                                    </div>

                                    <div className="row wellcome-title">
                                        Welcome to Know Me!
                                        <br />
                                        Sign in to see latest updates.
                                    </div>

                                    <div className="row social-network-title">
                                        sign in with your social network
                                    </div>

                                    <div className="row social-network-buttons-contaner">

                                            <a
                                                className="btn btn-danger button-container"
                                                onClick={this.signInWithGoogle}
                                            >
                                                <FontAwesomeIcon
                                                    title="Google"
                                                    icon={['fab', 'google']}
                                                />
                                                &nbsp;
                                                Google
                                            </a>

                                            <a
                                                className="btn btn-facebook button-container"
                                                onClick={this.signInWithFacebook}
                                            >
                                                <FontAwesomeIcon
                                                    title="Fasbook"
                                                    icon={['fab', 'facebook-f']}
                                                />
                                                &nbsp;
                                                Facebook
                                            </a>
                                    </div>

                                    <div className="row or-devided">
                                        <div className="col-5">
                                            <hr />
                                        </div>

                                        <div className="col-2">
                                            or
                                        </div>

                                        <div className="col-5">
                                            <hr />
                                        </div>
                                    </div>

                                    <form noValidate autoComplete="off">
                                   
                                        <TextField
                                            id="Email"
                                            label="Email"
                                            fullWidth
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                            margin="dense"
                                            onChange={e=>this.setState({email:e.target.value})}
                                            value={this.state.email}
                                            helperText={this.state.emailValidation}
                                            onBlur={this.emailValidation}
                                        />

                                        <TextField
                                            id="Password"
                                            label="Password"
                                            fullWidth
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                            InputProps={{
                                                endAdornment: (
                                                <InputAdornment
                                                    position="end"
                                                    onClick={e=>this.setState({showPasword:!this.state.showPasword})}
                                                >
                                                    {
                                                        this.state.showPasword?
                                                        <VisibilityOffIcon/>:
                                                        <VisibilityIcon />
                                                    }
                                                </InputAdornment>
                                                ),
                                            }}
                                            margin="dense"
                                            type={this.state.showPasword? "text":"password"}
                                            onChange={e=>this.setState({password:e.target.value})}
                                            value={this.state.password}
                                            helperText={this.state.passwordValidation}
                                            onBlur={this.passwordValidation}
                                        />

                                        <div className="row">
                                            <div className="col-6 remember-me">
                                                <FormControlLabel
                                                    control={
                                                        <Checkbox
                                                            icon={<CheckBoxOutlineBlankIcon fontSize="small" />}
                                                            checkedIcon={<CheckBoxIcon fontSize="small" />}
                                                            checked={this.state.RememberMe}
                                                            color="secondary"
                                                            name="rememberMe"
                                                            onChange={e => this.setState({ RememberMe: !this.state.RememberMe })}
                                                        />
                                                    }
                                                    label="Remember me"
                                                />
                                            </div>
                                            <div className="col-6 recover-password">
                                                <a onClick={e=>this.resetPassword()}>
                                                    Recover password
                                                </a>
                                            </div>
                                        </div>

                                        <div className="row sign-in-up-button-container" >
                                            {
                                                this.state.isSignIn?
                                                <button
                                                    disabled={
                                                        this.state.emailValidation===""&&this.state.passwordValidation===""&&
                                                        this.state.email!==""&&this.state.password!==""?
                                                        false:true
                                                    }
                                                    type="button"
                                                    className="col-xl-4 col-lg-4 col-md-4 col-sm-12 btn btn-pink"
                                                    onClick={e=>this.signIn()}
                                                >
                                                    Sign In
                                                </button>:
                                                <button
                                                    disabled={
                                                        this.state.emailValidation===""&&this.state.passwordValidation===""&&
                                                        this.state.email!==""&&this.state.password!==""?
                                                        false:true
                                                    }
                                                    type="button"
                                                    className="col-xl-4 col-lg-4 col-md-4 col-sm-12 btn btn-pink"
                                                    onClick={e=>this.signUp()}
                                                >
                                                    Sign Up
                                                </button>
                                            }

                                        </div>
                                
                                    </form>
                                </>
                            }
                        </div>
                    </div>
                </div>
            );

    }

};

const mapStateToProps=(state)=>{
    return{
        login:state.login,
        load: state.card.load,

    }
    
}
const mapDispatchToProps = (dispatch) => {
    return {
        onAuthStateChanged: () => dispatch(actions.onAuthStateChanged()),
        setMessage:(message) => dispatch(actions.setMessage(message)),
        setMessageStatus:(status) => dispatch(actions.setMessageStatus(status))
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(Login)





