import produce from 'immer';
import createReducer from "./reducerUtils";

const initialState = {

    isShowUserNameForm:false,
    message:"",
    messageStatus:false
};

const login = {
    setShowUserNameForm(state, action) {
        state.isShowUserNameForm = action.payload;
    },
    setMessage(state, action) {
        state.message = action.payload;
        state.messageStatus = true;
    },
    setMessageStatus(state, action) {
        state.messageStatus = action.payload;
    },
    
}

export default produce((state, action) => createReducer(state, action, login), initialState);
