import React from 'react';
import { actions } from '../../Redux/actions/Action'
import { connect } from 'react-redux';
import './Reveiws.css'
import Carousel from 'react-elastic-carousel';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function mapStateToProps(state) {

    return {
        screenSize: state.card.screenSize,
        reveiw: state.reveiws.currentReviews
    };
}
function mapDispatchToProps(dispatch) {
    return {
        setFullNameReveiws: (name) => dispatch(actions.setFullNameReveiws(name)),
        setRuleReveiws: (name) => dispatch(actions.setRuleReveiws(name)),
        setMessageReveiws: (name) => dispatch(actions.setMessageReveiws(name)),
        setButtonReveiws: (name) => dispatch(actions.setButtonReveiws(name)),
        setIndexReveiws: (name) => dispatch(actions.setIndexReveiws(name)),
        addNewReveiws: (name) => dispatch(actions.addNewReveiws(name)),
        setCoverReveiwsStyle: (style) => dispatch(actions.setCoverReveiwsStyle(style)),
        deleteReveiw: (index) => dispatch(actions.deleteReveiw(index)),
    };
}
export default connect(mapStateToProps, mapDispatchToProps)(function Reveiw(props) {
    const {
        reveiw,
        showState,
        preview,
        setFullNameReveiws,
        setRuleReveiws,
        setMessageReveiws,
        setIndexReveiws,
        addNewReveiws,
        screenSize,
        setImgModal,
        setCoverReveiwsStyle,
        deleteReveiw

    } = props;
    const [autoPlaySpeed, setautoPlaySpeed] = React.useState(false);


    const onChangeReveiws = (Index) => {
        setIndexReveiws(Index);
        let num
    }
    const onNextEnd = () => {
        setautoPlaySpeed(true);
    }
    const onPrevStart = (prevItemObject, nextItemObject) => {
        setautoPlaySpeed(false);
    }

    return (

        <div>
            {reveiw.reveiwsList.length !== 0 ?
                <Carousel
                    Style={{ color: "red!important" }}
                    color="red"
                    itemsToShow={screenSize === 0 ? 1 : 3}
                    pagination={false}
                    disableArrowsOnEnd={false}
                    autoPlaySpeed={50}
                    enableAutoPlay={autoPlaySpeed}
                    onPrevStart={onPrevStart}
                    renderArrow={({ type, onClick }) => <div style={{ color: reveiw.styleColorArrow, marginTop: "45px" }} onClick={onClick}> {type === 'PREV' ?
                        <FontAwesomeIcon icon={['fas', 'angle-left']}></FontAwesomeIcon> : <FontAwesomeIcon icon={['fas', 'angle-right']}></FontAwesomeIcon>
                    }
                    </ div>}


                >
                    {reveiw.reveiwsList.map((reveiwsList, i) => {
                        let index = i;
                        return <div key={i} onClick={!showState ? (e) => { onChangeReveiws(index); } : null} className=" row borderReveiwsBody " style={reveiwsList.reveiwsStyle ? { background: reveiwsList.reveiwsStyle.bodyReveiws.background } : ""}
                        >
                            <div className="col d-flex flex-column">
                                <div className="row d-flex justify-content-start">
                                    <div className="p-2" style={{ width: "45px" }}>
                                        <div
                                            title="change profile img"
                                            className="defult__profile_reveiws "
                                            id="profile"
                                            style={reveiwsList.reveiwsStyle.profile_reveiws}
                                            onClick={!showState ? () => {
                                                setImgModal({
                                                    show: true,
                                                    imgId: "#fileLoaderReveiws",
                                                    func: setCoverReveiwsStyle,
                                                    type: 'div',
                                                    aspectX: 3,
                                                    aspectY: 3,
                                                    cropShape: 'round',
                                                    currBackground: reveiwsList.reveiwsStyle.profile_reveiws.backgroundImage,
                                                })
                                            } : ""}
                                        >
                                        </div>
                                    </div>

                                    <div className=" p-2 reviews-description" >
                                        {!preview && !showState ?
                                            <input
                                                className=" m-0 p-0 input_reveiws "
                                                placeholder={reveiwsList.reveiwsDetails.full_name !== "" ? "" : "Recommend Name"}
                                                value={reveiwsList.reveiwsDetails.full_name !== "" ? reveiwsList.reveiwsDetails.full_name : "Recommend Name"}
                                                onChange={(e) => { setFullNameReveiws(e.target.value); }}
                                                onClick={(e) => { onChangeReveiws(index); }}                                        // hidden={!card.currentCard.vCardStyle.fullNameShow}
                                                style={reveiwsList.reveiwsStyle.input_card_fullName}
                                                title={preview || showState ? "" : "Change Name"}
                                            // id={showState ? "fullName_p" : "fullName"}
                                            />
                                            :
                                            <label className="m-0 p-0 input_reveiws"
                                                style={{
                                                    color: "#000000",
                                                    // textAlign: cssObj_reveiws.input_card_fullName.textAlign,
                                                    font: reveiwsList.reveiwsStyle.input_card_fullName.textAlign,
                                                    letterSpacing: reveiwsList.reveiwsStyle.input_card_fullName.letterSpacing,
                                                    textTransform: reveiwsList.reveiwsStyle.input_card_fullName.textTransform,
                                                    fontSize: reveiwsList.reveiwsStyle.input_card_fullName.fontSize
                                                }}
                                            >
                                                {
                                                    reveiwsList.reveiwsDetails.full_name !== "" ?
                                                        reveiwsList.reveiwsDetails.full_name : "Name"
                                                }

                                            </label>
                                        }
                                        {!preview && !showState ?
                                            reveiw.roleShow ?
                                                <input
                                                    autoComplete="new-password "
                                                    className="p-0 input_reveiws "
                                                    placeholder={reveiwsList.reveiwsDetails.role !== "" ? "" : "Profession"}
                                                    value={reveiwsList.reveiwsDetails.role !== "" ? reveiwsList.reveiwsDetails.role : "Profession"}
                                                    onChange={(e) => { setRuleReveiws(e.target.value) }}
                                                    onClick={(e) => { onChangeReveiws(index); }}                                        // hidden={!card.currentCard.vCardStyle.fullNameShow}
                                                    hidden={!reveiw.roleShow}
                                                    style={reveiwsList.reveiwsStyle.input_card_rule}
                                                    title={preview || showState ? "" : "Change role"}
                                                // id={showState ? "role_p" : "role"}
                                                ></input> : ""
                                            : <label className=" m-0 p-0 input_reveiws "
                                                style={{
                                                    color: "#000000",
                                                    // textAlign: cssObj_reveiws.input_card_rule.textAlign,
                                                    font: reveiwsList.reveiwsStyle.input_card_rule.textAlign,
                                                    letterSpacing: reveiwsList.reveiwsStyle.input_card_rule.letterSpacing,
                                                    textTransform: reveiwsList.reveiwsStyle.input_card_rule.textTransform,
                                                    fontSize: reveiwsList.reveiwsStyle.input_card_rule.fontSize,
                                                }}

                                            >
                                                {
                                                    reveiwsList.reveiwsDetails.role !== "" ? reveiwsList.reveiwsDetails.role : "Profession"
                                                }
                                            </label>
                                        }

                                    </div>
                                    <div className="col-1 pl-4 pt-2">
                                        {!preview || !showState ?
                                            <FontAwesomeIcon
                                                className="delete-icon"
                                                title="Delete Reveiws"
                                                color="gray"
                                                size="xs"
                                                onClick={(e) => { deleteReveiw(index) }}
                                                icon={['fas', 'trash']}
                                            ></FontAwesomeIcon> : ""}

                                    </div>
                                </div>
                                {!preview && !showState ?
                                    <div className="row rounded d-flex flex-column pb-2 h-100">
                                        <textarea
                                            id="massegeBody"
                                            className="rounded textReveiws pl-3 pr-3 pb-3"
                                            style={reveiwsList.reveiwsStyle.message_reveiws_background}
                                            value={reveiwsList.reveiwsDetails.message_reveiws}
                                            onChange={(e) => setMessageReveiws(e.target.value)}
                                            // onClick={setFooterStyleToggle}
                                            placeholder="Review..."
                                            title={preview || showState ? "" : "Change Message"}
                                        >
                                        </textarea>
                                    </div> : <div className="row pl-2 pr-2 pb-2"
                                        style={{
                                            background: reveiwsList.reveiwsStyle.message_reveiws_background.background,
                                            fontSize: reveiwsList.reveiwsStyle.message_reveiws_background.fontSize,
                                            border: reveiwsList.reveiwsStyle.message_reveiws_background.border,
                                            color: reveiwsList.reveiwsStyle.message_reveiws_background.color,
                                            lineHeight: reveiwsList.reveiwsStyle.message_reveiws_background.lineHeight,
                                            height: reveiwsList.reveiwsStyle.message_reveiws_background.height
                                        }}>{reveiwsList.reveiwsDetails.message_reveiws}</div>}
                            </div>
                        </div>
                    })}
                </Carousel> : ""}
            {
                !preview && !showState ?
                    <div className="d-flex justify-content-center mt-2">
                        <button
                            className="d-flex justify-content-center btn-reveiw-mobile"
                            onClick={(e) => { addNewReveiws(); onNextEnd(reveiw.IndexReveiws, (reveiw.reveiwsList.length - 1)) }}
                        >
                            Add New Reveiw +
                        </button>
                    </div> : ""
            }
        </div >
    );
});



